a {
  text-decoration: none;
  color: #E0ECFF;
}
.governance-box {
  border-top: 1px solid #203B60;
}
.governance-box .check {
  display: flex;
  margin-top: 18px;
  margin-left: 45px;
}
.governance-box .check div {
  width: 88px;
  height: 31px;
  background: rgba(85, 119, 253, 0.1);
  margin-left: 7px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #E0ECFF;
  cursor: pointer;
}
.governance-box .check div a {
  text-decoration: none;
  color: #E0ECFF;
}
.governance-box .check .now {
  background: #5577FD;
}
.governance-box .now-prom {
  margin-top: 28px;
  margin-left: 50px;
}
.governance-box .finished-box {
  width: 1140px;
  height: 790px;
  border: 1px solid #203B60;
  margin: 0 auto;
  margin-top: 48px;
  font-weight: normal;
  background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);
}
.governance-box .finished-box .finished-title {
  margin-left: 32px;
  margin-top: 23px;
}
.governance-box .finished-box .finished-title .title-text {
  font-size: 32px;
  color: #FFFFFF;
}
.governance-box .finished-box .finished-title .title-describe {
  margin-top: 16px;
  font-size: 14px;
}
.governance-box .finished-box .finished-detail {
  margin-top: 24px;
}
.governance-box .finished-box .finished-detail .finished-detail-box {
  width: 1138px;
  height: 136px;
  display: flex;
  border: 1px solid #203B60;
  border-radius: 4px;
  margin-top: 24px;
  background: rgba(85, 119, 253, 0.1);
}
.governance-box .finished-box .finished-detail .finished-detail-box .finished-detail-box-describe,
.governance-box .finished-box .finished-detail .finished-detail-box .finished-detail-box-title {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #E0ECFF;
}
.governance-box .finished-box .finished-detail .finished-detail-box .finished-detail-box-title {
  width: 160px;
  border-right: 1px solid #203B60;
  font-size: 32px;
  font-weight: 600;
}
.governance-box .finished-box .finished-detail .finished-detail-box .finished-detail-box-describe {
  width: 976px;
  font-size: 24px;
}
.governance-box .finished-box .finished-detail .finished-detail-box .finished-detail-box-describe span {
  width: 855px;
}
@media only screen and (min-width: 1085px) and (max-width: 1140px) {
  .governance-box .finished-box {
    width: 1085px;
  }
  .governance-box .finished-box .finished-detail .finished-detail-box {
    width: 1083px;
  }
  .governance-box .finished-box .finished-detail .finished-detail-box .finished-detail-box-title {
    width: 153px;
  }
  .governance-box .finished-box .finished-detail .finished-detail-box .finished-detail-box-describe {
    width: 933px;
  }
}
@media only screen and (min-width: 760px) and (max-width: 1085px) {
  .governance-box .finished-box {
    width: 711px;
  }
  .governance-box .finished-box .finished-detail .finished-detail-box {
    width: 709px;
  }
  .governance-box .finished-box .finished-detail .finished-detail-box .finished-detail-box-title {
    width: 133px;
  }
  .governance-box .finished-box .finished-detail .finished-detail-box .finished-detail-box-describe {
    width: 579px;
  }
  .governance-box .finished-box .finished-detail .finished-detail-box .finished-detail-box-describe span {
    width: 558px;
  }
}
@media only screen and (max-width: 760px) {
  .governance-box .finished-box {
    width: 326px;
    border: 0;
  }
  .governance-box .finished-box .finished-detail .finished-detail-box {
    display: block;
    width: 324px;
    height: 338px;
  }
  .governance-box .finished-box .finished-detail .finished-detail-box .finished-detail-box-title {
    width: 324px;
    height: 88px;
    border: 0;
    display: block;
    line-height: 88px;
    padding-left: 24px;
    border-bottom: 1px solid #203B60;
  }
  .governance-box .finished-box .finished-detail .finished-detail-box .finished-detail-box-describe {
    width: 324px;
    display: block;
    padding: 0 24px;
    padding-top: 64px;
  }
  .governance-box .finished-box .finished-detail .finished-detail-box .finished-detail-box-describe span {
    width: 226px;
  }
  .governance-box .finished-box .finished-detail .dip-one {
    height: 401px;
  }
}
